import TableList from "./TableList";
import TableListData from "./TableListData";
import "./App.css";
import { useState } from "react";

function HomeTable({
  dataHome,
  handleArray,
  dataList,
  defaultValue,
  getIndex,
  handleDnd,
}) {
  const [hideEdit, isHideEdit] = useState(false);
  const handleSave = (bool) => {
    isHideEdit(bool);
    handleDnd(false);
  };
  return (
    <div className="homeTable-container">
      <TableList
        AddArray={handleArray}
        listData={dataList}
        defaultValue={defaultValue}
        handleSave={handleSave}
        handleDnd={handleDnd}
      />

      {hideEdit && (
        <TableListData
          dataList={dataList}
          dataObject={dataHome}
          getIndex={getIndex}
        />
      )}
    </div>
  );
}

export default HomeTable;
