import "./App.css";

function TableListData({ dataObject, dataList }) {
  return (
    <div className="TableListData-container">
      <h1>Table Data</h1>
      <div className="data-header">
        {dataObject.map((header) => {
          return (
            <ul>
              <li>{header}</li>
            </ul>
          );
        })}
      </div>

      <div className="data-inputs">
        {dataList.map((list, id) => {
          console.log(dataList);
          return (
            <ul key={id}>
              <li>{list.id}</li>
              <li>{list.title}</li>
              <li>{list.qty}</li>
              <li>{list.category}</li>
              <li>
                <span>Edit</span>
                <span> </span>
                <span>Delete</span>
              </li>
            </ul>
          );
        })}
      </div>
    </div>
  );
}

export default TableListData;
